import {
  Component,
  Element,
  Event,
  EventEmitter,
  h,
  Host,
  Method,
  Prop,
  State,
} from '@stencil/core';
import { ToastType } from './toast-utils';
import {
  iconClose,
  iconError,
  iconInfo,
  iconSuccess,
  iconWarning,
} from '@siemens/ix-icons/icons';

@Component({
  tag: 'ix-toast',
  styleUrl: 'toast.scss',
  shadow: true,
})
export class Toast {
  /**
   * Toast type
   */
  @Prop() type: ToastType = 'info';

  /**
   * Toast title
   */
  @Prop() toastTitle?: string;

  /**
   * Autoclose title after delay
   */
  @Prop() autoCloseDelay = 5000;

  /**
   * Autoclose behavior
   */
  @Prop() preventAutoClose = false;

  /**
   * Icon of toast
   */
  @Prop() icon?: string;

  /**
   * Icon color of toast
   */
  @Prop() iconColor?: string;

  /**
   * ARIA label for the close icon button
   * Will be set as aria-label on the nested HTML button element
   *
   * @since 3.2.0
   */
  @Prop() ariaLabelCloseIconButton?: string;

  /**
   * Toast closed
   */
  @Event() closeToast!: EventEmitter;

  @State() progress = 0;
  @State() touched = false;
  @State() paused = false;

  @Element() hostElement!: HTMLIxToastElement;

  private getIcon() {
    if (this.icon) {
      return (
        <ix-icon
          data-testid="toast-icon"
          name={this.icon}
          color={this.iconColor}
          size="24"
        />
      );
    }

    switch (this.type) {
      case 'info':
        return (
          <ix-icon
            data-testid="toast-icon"
            name={iconInfo}
            size="24"
            color="color-std-text"
          />
        );

      case 'error':
        return (
          <ix-icon
            data-testid="toast-icon"
            name={iconError}
            size="24"
            color="color-alarm"
          />
        );

      case 'success':
        return (
          <ix-icon
            data-testid="toast-icon"
            name={iconSuccess}
            size="24"
            color="color-success"
          />
        );

      case 'warning':
        //TODO(IX-3400): Replace icon colors with proper CSS variables when available
        return (
          <ix-icon
            data-testid="toast-icon"
            name={iconWarning}
            size="24"
            color="color-warning-text"
          />
        );

      default:
        return '';
    }
  }

  private close() {
    if (this.hostElement) {
      this.hostElement.classList.add('animate__fadeOut');
    }
    setTimeout(() => {
      this.closeToast.emit();
    }, 250);
  }

  /**
   * Pause the toast's auto-close progress bar and timer.
   */
  @Method()
  async pause() {
    this.paused = true;
  }

  /**
   * Resume the toast's auto-close progress bar and timer if previously paused.
   */
  @Method()
  async resume() {
    this.paused = false;
  }

  /**
   * Returns whether the toast is currently paused (auto-close is paused).
   */
  @Method()
  async isPaused(): Promise<boolean> {
    return this.paused || this.touched;
  }

  render() {
    let progressBarStyle: Record<string, string> = {};

    const progressBarClass = ['toast-progress-bar'];

    progressBarStyle = {
      animationDuration: `${this.autoCloseDelay}ms`,
      animationPlayState: this.touched || this.paused ? 'paused' : 'running',
    };

    progressBarClass.push('toast-progress-bar--animated');

    return (
      <Host class="animate__animated animate__fadeIn">
        <div
          class="toast-body"
          onPointerLeave={() => {
            this.touched = false;
          }}
          onPointerEnter={() => {
            this.touched = true;
          }}
        >
          {this.type || this.icon ? (
            <div class="toast-icon">{this.getIcon()}</div>
          ) : null}
          <div class="toast-content">
            {this.toastTitle ? (
              <ix-typography class="toast-title" format="h5">
                {this.toastTitle}
              </ix-typography>
            ) : null}
            <div class="toast-message">
              <slot></slot>
            </div>
            <div class="toast-action">
              <slot name="action"></slot>
            </div>
          </div>
          <div class="toast-close">
            <ix-icon-button
              icon={iconClose}
              iconColor="color-soft-text"
              size="24"
              variant="tertiary"
              onClick={() => this.closeToast.emit()}
              aria-label={this.ariaLabelCloseIconButton}
            />
          </div>
        </div>
        {!this.preventAutoClose && (
          <div
            class={progressBarClass.join(' ')}
            style={progressBarStyle}
            onAnimationEnd={() => {
              this.close();
            }}
            onTransitionEnd={() => {
              if (this.progress === 0) {
                this.close();
              }
            }}
          ></div>
        )}
      </Host>
    );
  }
}
